function asyncComponent(path) {
    return () =>
      import('./'+path + '.vue')
  }
// import Main from './views/main.vue';

export default new VueRouter({
    mode: 'hash',
    routes: [
        {
          path: '*',
          component: asyncComponent('components/404'),
          meta: {
            title: '404'
          }
        },
        {
          path: '',
          component: asyncComponent('views/main'),
          name: 'admin',
          children:[
            {
              path:'/popularList',
              component: asyncComponent('views/popular/popularList'),
              name: '舆情管理'
            },
            {
              path:'/addPopular',
              component: asyncComponent('views/popular/addPopular'),
              name: '添加舆情'
            },
            {
              path:'/infoList',
              component: asyncComponent('views/info/infoList'),
              name: '资讯管理'
            },
            {
              path:'/addinfo',
              component: asyncComponent('views/info/addinfo'),
              name: '添加资讯'
            },
            {
              path:'/outfitList',
              component: asyncComponent('views/outfit/outfitList'),
              name: '机构观点'
            },//机构观点
            {
              path:'/addOutfit',
              component: asyncComponent('views/outfit/addOutfit'),
              name: '添加机构观点'
            },//添加机构观点
            {
              path:'/boomList',
              component: asyncComponent('views/boom/boomList'),
              name: '景气指数观点'
            },//景气指数观点
            {
              path:'/addBoom',
              component: asyncComponent('views/boom/addBoom'),
              name: '添加景气指数观点'
            },//添加景气指数观点
            {
              path:'/basicList',
              component: asyncComponent('views/basic/basicList'),
              name: '基本面观点'
            },//基本面观点
            {
              path:'/addBasic',
              component: asyncComponent('views/basic/addBasic'),
              name: '添加基本面观点'
            },//添加基本面观点
            {
              path:'/outfitManageList',
              component: asyncComponent('views/outfitManage/outfitManageList'),
              name: '机构管理'
            },//机构管理 
            {
              path:'/addOutfitManage',
              component: asyncComponent('views/outfitManage/addOutfitManage'),
              name: '添加机构'
            },//添加机构     
            {
              path:'/eventManageList',
              component: asyncComponent('views/eventManage/eventManageList'),
              name: '事件管理'
            },//事件管理   
            {
              path:'/addEventManage',
              component: asyncComponent('views/eventManage/addEventManage'),
              name: '添加事件'
            },//添加事件  
            {
              path:'/percentList',
              component: asyncComponent('views/percent/percentList'),
              name: '舆情指数管理'
            },//事件管理   
            {
              path:'/percentadd',
              component: asyncComponent('views/percent/percentadd'),
              name: '添加舆情指数'
            },//添加事件  
      
          ]
        }
      ]
})
